#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-logo {
  position: fixed;
  right: 0;
  bottom: 0;
  margin: 20px;
  widows: 100px;
  height: 100px;
}

.nyt {
  position: fixed;
  left: 0;
  bottom: 0;
  margin: 30px;
  widows: 75px;
  height: 75px;
}

.butn {
  position: fixed;

  widows: 75px;
  height: 75px;
}

#slideContainer {
  margin-left: 25%;
  margin-right: 25%;
  bottom: 5%;
  position: fixed;
  width: 50%;
}

.dateDisplay {
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 3%;
}

.map-overlay {
  position: absolute;
  bottom: 20%;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
  margin-right: 20px;
  font-family: Arial, sans-serif;
  overflow: auto;
  border-radius: 3px;
  text-align: left;
}

#legend {
  padding: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  line-height: 18px;
  height: 200px;
  margin-bottom: 40px;
  width: 100px;
}
.legend-key {
  display: inline-block;
  border-radius: 20%;
  width: 10px;
  height: 10px;
  margin-right: 5px;
  background-color: red;
}

#features {
  top: 0;
  height: 100px;
  margin-top: 20px;
  width: 250px;
}

#LegendItem1 {
  background-color: white;
}

#LegendItem2 {
  background-color: #faf769;
}

#LegendItem3 {
  background-color: #f5bc4c;
}

#LegendItem4 {
  background-color: #f7983e;
}

#LegendItem5 {
  background-color: #f7723e;
}

#LegendItem6 {
  background-color: #f51d1d;
}
